.App {
  text-align: center;

}
html{
  border-style: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html, body{
  min-height: 100%;
  width: 100%;
  padding: 0px;
  margin: 0px;
  background-color: #82ccdd;

}
.up{
  position: fixed;
  top: 70vh;
  left: 15px;
  font-size: 1.5rem;
}
  
